<script>
import ListItem from '../components/itemSelected.vue'
import Phone from '../components/phone.vue'

export default {
		name: 'Docs',
		components: {
			'list-item': ListItem,
			'phone-show': Phone
		},
		computed: {
			name () {
				return `${window.location.origin}/#/phone/${this.$route.name}`
			},
			showPhone () {
				let temp = new Set(['des', 'usage'])
				return !temp.has(this.$route.name)
			}
		}
}
